(async($) => {

    await new Promise((resolve) => {
        if (CryptoPay.providers.length > 0) {
            let providers = CryptoPay.providers;
            CryptoPay.providers = {}
            providers.forEach(function (provider, index) {
                CryptoPay.providers[provider.toLowerCase()] = window[provider];
                if (index === providers.length - 1) resolve(CryptoPay.providers);
            });
        } else {
            resolve(CryptoPay.providers = {});
        }
    });

    CryptoPay.startPayment = (order, params) => {
        if (!order.amount) {
            throw new Error('Order amount is required');
        }

        if (!order.currency) {
            throw new Error('Order currency is required');
        }

        return window.CryptoPayApp = initCryptoPay('cryptopay', Object.assign(CryptoPay, {order, params}));
    }

    if (CryptoPay.autoLoad) {
        window.CryptoPayApp = initCryptoPay('cryptopay', CryptoPay);
    }

    if (CryptoPay.checkoutPage) {

        $form = $('form.checkout');

        function handleUnloadEvent() {
            if((navigator.userAgent.indexOf('MSIE') !== -1 ) || (!!document.documentMode)) {
                // IE handles unload events differently than modern browsers
                e.preventDefault();
                return undefined;
            }

            return true;
        }

        function scrollToNotices() {
            var scrollElement = $('.woocommerce-NoticeGroup-updateOrderReview, .woocommerce-NoticeGroup-checkout');
    
            if ( ! scrollElement.length ) {
                scrollElement = $form;
            }
            
            if ( scrollElement.length ) {
                $( 'html, body' ).animate( {
                    scrollTop: ( scrollElement.offset().top - 100 )
                }, 1000 );
            }
        }

        function printError(errorMessage) {
			$('.woocommerce-NoticeGroup-checkout, .woocommerce-error, .woocommerce-message').remove();
			$form.prepend('<div class="woocommerce-NoticeGroup woocommerce-NoticeGroup-checkout">' + errorMessage + '</div>');
			$form.removeClass('processing').unblock();
			$form.find('.input-text, select, input:checkbox').trigger('validate').trigger('blur');
			scrollToNotices();
			$(document.body).trigger('checkout_error' , [errorMessage]);
		}

        function checkFormProcess(network) {
            window.cpHelpers.waitingPopup(CryptoPay.lang.checkingForm);
            return new Promise((resolve) => {
                $.ajax({
					type:		'POST',
					url:		wc_checkout_params.checkout_url,
					data:		$form.serialize(),
					dataType:   'json',
					success:	function(response) {
                        $(window).off('beforeunload', handleUnloadEvent);

                        if (response.success) {
                            $(".woocommerce-NoticeGroup-checkout").remove();
                            resolve(null);
                        } else {
                            if (true === response.data.reload) {
								window.location.reload();
								return;
							}

							if (true === response.data.refresh) {
								$(document.body).trigger('update_checkout');
							}

							if (response.message) {
								printError(response.message);
							} else {
								printError('<div class="woocommerce-error">' + CryptoPay.lang.defaultErrorMsg + '</div>'); 
							}

                            if (network.paymentType === 'qr') {
                                return resolve({error: true, message: CryptoPay.lang.qrCodePaymentMsg});
                            }

                            resolve({error: true});
                        }
					},
					error:	function(jqXHR, textStatus, errorThrown) {
                        $(window).off('beforeunload', handleUnloadEvent);

						printError(
							'<div class="woocommerce-error">' +
							(errorThrown || CryptoPay.lang.defaultErrorMsg) +
							'</div>'
						);

                        resolve({
                            error: true,
                        });
					}
				});
            });
        }

        CryptoPay.hooks.beforeThanPayNow = (network) => {
            return checkFormProcess(network);
        }

        CryptoPay.hooks.beforeThanQrPaymentConfirmation = (network) => {
            return checkFormProcess(network);
        }

        $(document).on('updated_checkout', function () {
            if (window.CryptoPayApp) {
                if (window.CryptoPayApp.store) {
                    let selectedNetwork = window.CryptoPayApp.store.getters.getSelectedNetwork;
                    if (
                        selectedNetwork &&
                        selectedNetwork.paymentType === 'qr' && 
                        window.qrVerifierInterval
                    ) {
                        clearInterval(window.qrVerifierInterval);
                    }
                }
                window.CryptoPayApp.reset();
            }
            window.CryptoPayApp = initCryptoPay('cryptopay', CryptoPay);
        });
    
        $('form.checkout').on('change', 'input[name="payment_method"]', function() {
            $(document.body).trigger('update_checkout');
        });
    }

})(jQuery);